#copyright-label {
    font-size: small;
}
#skip-button {
    transition:
        text-shadow 0.15s linear 0s,
        -webkit-transform 0.15s linear 0s;
    transition:
        transform 0.15s linear 0s,
        text-shadow 0.15s linear 0s;
    transition:
        transform 0.15s linear 0s,
        text-shadow 0.15s linear 0s,
        -webkit-transform 0.15s linear 0s;
}

#skip-button:hover {
    -webkit-transform: translate(-3px, -3px);
            transform: translate(-3px, -3px);
    text-shadow: 3px 3px #00000044;
}

@-webkit-keyframes blink {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0.15;
    }
}

@keyframes blink {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0.15;
    }
}

#continue-label {
    -webkit-animation: blink 0.65s ease-in 0s infinite alternate;
            animation: blink 0.65s ease-in 0s infinite alternate;
}
